.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-height: 100vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.map {
  width: 70%;
  min-width: 345px;

}

/* Wanring authentication*/
.warning-title {
  color: red;
}

.warning-message {
  font-weight:bold;
}

.warning-dialog {
  margin: 0;
}
/* Location List personalization */

.filter {
  width: 30%;
}

input {
  width: 75%;
}

/* List Location*/

ul.locations-list  {
  padding : 0;
  min-width: 150px;
  width: 100%;
}


li.location {
  list-style-type: none;

}

/* InfoWindow Dialog */
.infowwindow {
  max-width: 50%;
}

.infowwindow-dialog {
  width: 200px;
}

/* For Device under 500px */

@media screen and ( max-width: 700px) {
  .filter,
  .map {
     width: 100%;
  }
}



